import cookie from "vux/src/tools/cookie/index.js"

const state = {
  language: "cn",
  boardStyle: {
    boardColor: "#F4D03F",
    lineColor: "#000000",
    lineWidth: 0.03,
    coordColor: "#000000",
    coordFontStyle: "",
    coordFontFamily: "sans-serif",
    starRadiusScale: 0.1,
    pieceBlack: "#000000",
    pieceWhite: "#FFFFFF",
    pieceStrokeWidth: 0.02,
    pieceStrokeBlack: "#000000",
    pieceStrokeWhite: "#000000",
    pieceScale: 0.96,
    indexColorBlack: "#FFFFFF",
    indexColorWhite: "#000000",
    indexColorLast: "#E74C3C",
    indexFontStyle: "bold",
    indexFontFamily: "sans-serif",
    indexScale: 0.45,
    lastStepColor: "#E74C3C",
    lastStepScale: 0.15,
    winlineWidth: 0.15,
    winlineColor: "#2E86C1",
    bestMoveColor: "#E74C3C",
    thinkingMoveColor: "#3FF476",
    thoughtMoveColor: "#3C5EE7",
    lostMoveColor: "#FDFEFE",
    bestMoveScale: 0.12,
    realtimeMoveScale: 0.1,
    selectionStrokeWidth: 0.08,
    selectionStrokeColor: "#E74C3C"
  },
  boardSize: 15,
  thinkTimeOption: 1,
  turnTime: 5000,
  matchTime: 1000000,
  maxDepth: 64,
  maxKNodes: 0,
  nbest: 1,
  rule: 0, // 0- 1- 4- 5-
  configIndex: 0, // [0,2]
  clickCheck: 0, // 0- 1- 2-
  showCoord: true,
  showDetail: true,
  showIndex: true,
  showLastStep: true,
  showWinline: true,
  indexOrigin: 0,
  aiThinkBlack: false,
  aiThinkWhite: false
}

const propertiesToSave = [
  "language",
  "boardSize",
  "thinkTimeOption",
  "turnTime",
  "matchTime",
  "maxDepth",
  "maxKNodes",
  "nbest",
  "rule",
  "configIndex",
  "clickCheck",
  "showCoord",
  "showDetail",
  "showIndex",
  "showLastStep",
  "showWinline",
  "aiThinkBlack",
  "aiThinkWhite"
]

const Version = "0.14"

const getters = {
  turnTime: state => {
    let turn = [state.turnTime, 5000, 120000, 0]
    return turn[state.thinkTimeOption]
  },
  matchTime: state => {
    let match = [state.matchTime, 180000, 900000, 0]
    return match[state.thinkTimeOption]
  }
}

const mutations = {
  setValue(state, payload) {
    state[payload.key] = payload.value
    
    if (propertiesToSave.includes(payload.key)) {
      let stateToSave = {}
      for (let p of propertiesToSave) stateToSave[p] = state[p]
      cookie.set("GMKC_CFG_" + Version, JSON.stringify(stateToSave), {raw:true})
    }
  },
  setValueNoSave(state, payload) {
    state[payload.key] = payload.value
  },
  setBoardStyle(state, payload) {
    state.boardStyle[payload.key] = payload.value
  }
}

const actions = {
  readCookies({ commit }) {
    let json = cookie.get("GMKC_CFG_" + Version)
    if (!json) return
    
    let stateToRead = JSON.parse(json)
    for (let p of propertiesToSave)
      commit("setValueNoSave", { key: p, value: stateToRead[p] })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
