import { throttle, debounce } from "throttle-debounce";
import { mapState, mapGetters } from "vuex";

const paddingTop = 10,
  paddingBottom = 22;
const paddingX = 22;

function fillCircle(ctx, x, y, r) {
  const PI2 = 2 * Math.PI;
  ctx.beginPath();
  ctx.arc(x, y, r, 0, PI2);
  ctx.fill();
}

function drawBackground(ctx, style, w, h) {
  ctx.save();
  ctx.shadowOffsetX = ctx.shadowOffsetY = 2;
  ctx.shadowBlur = paddingX / 2;
  ctx.shadowColor = "rgba(0, 0, 0, 0.2)";
  ctx.fillStyle = style.boardColor;
  ctx.fillRect(
    paddingX,
    paddingTop,
    w - paddingX * 2,
    h - paddingBottom - paddingTop
  );
  //ctx.clearRect(padding, padding, w - padding * 2, w - padding * 2)
  ctx.restore();
}

function drawCoord(ctx, style, w, h, s, cs) {
  ctx.save();
  ctx.font =
    style.coordFontStyle +
    " " +
    Math.min(13, cs * 0.5) +
    "px " +
    style.coordFontFamily;
  ctx.textAlign = "center";
  ctx.textBaseline = "middle";
  ctx.fillStyle = style.coordColor;

  // 
  ctx.save();
  ctx.translate(paddingX / 2, paddingTop + cs / 2);
  for (let i = 0; i < s; i++) {
    ctx.fillText(s - i, 0, cs * i, paddingX);
  }

  // 
  ctx.translate(w - paddingX, 0);
  for (let i = 0; i < s; i++) {
    ctx.fillText(s - i, 0, cs * i, paddingX);
  }
  ctx.restore();

  // 
  ctx.translate(paddingX + cs / 2, h - paddingBottom / 2);
  for (let i = 0; i < s; i++) {
    ctx.fillText(String.fromCharCode("A".charCodeAt(0) + i), cs * i, 0);
  }

  ctx.restore();
}

function drawBoard(ctx, style, s, cs) {
  let si = s - 1;

  ctx.save();
  ctx.strokeStyle = ctx.fillStyle = style.lineColor;
  ctx.translate(paddingX + cs / 2, paddingTop + cs / 2);
  ctx.scale(cs, cs);

  // 
  ctx.lineWidth = style.lineWidth;
  ctx.beginPath();
  for (let i = 1; i < si; i++) {
    ctx.moveTo(i, 0);
    ctx.lineTo(i, si);
    ctx.moveTo(0, i);
    ctx.lineTo(si, i);
  }
  ctx.stroke();

  // 
  ctx.lineWidth = style.lineWidth * 2.5;
  ctx.beginPath();
  ctx.moveTo(0, 0);
  ctx.lineTo(si, 0);
  ctx.lineTo(si, si);
  ctx.lineTo(0, si);
  ctx.closePath();
  ctx.stroke();

  // 
  let starPad = Math.floor(s / 5);
  let starCenter = Math.floor(s / 2);
  let starRadius = style.starRadiusScale;
  fillCircle(ctx, starPad, starPad, starRadius);
  fillCircle(ctx, si - starPad, starPad, starRadius);
  fillCircle(ctx, starPad, si - starPad, starRadius);
  fillCircle(ctx, si - starPad, si - starPad, starRadius);
  fillCircle(ctx, starCenter, starCenter, starRadius);

  ctx.restore();
}

function drawPiece(ctx, style, cs, position) {
  let radius = style.pieceScale / 2;
  ctx.save();
  ctx.translate(paddingX + cs / 2, paddingTop + cs / 2);
  ctx.scale(cs, cs);

  ctx.lineWidth = style.pieceStrokeWidth;
  ctx.fillStyle = style.pieceBlack;
  ctx.strokeStyle = style.pieceStrokeBlack;
  for (let i = 0; i < position.length; i += 2) {
    let pos = position[i];
    fillCircle(ctx, pos[0], pos[1], radius);
    if (style.pieceStrokeWidth > 0) ctx.stroke();
  }

  ctx.fillStyle = style.pieceWhite;
  ctx.strokeStyle = style.pieceStrokeWhite;
  for (let i = 1; i < position.length; i += 2) {
    let pos = position[i];
    fillCircle(ctx, pos[0], pos[1], radius);
    if (style.pieceStrokeWidth > 0) ctx.stroke();
  }

  ctx.restore();
}

function drawIndex(ctx, style, cs, position, startIndex, highlight) {
  ctx.save();
  ctx.font =
    style.indexFontStyle +
    " " +
    style.indexScale * cs +
    "px " +
    style.indexFontFamily;
  ctx.textAlign = "center";
  ctx.textBaseline = "middle";
  ctx.translate(paddingX + cs / 2, paddingTop + cs / 2);

  for (let i = startIndex; i < position.length - 1; i += 1) {
    let pos = position[i];
    let text = i - startIndex + 1;
    ctx.fillStyle = i % 2 == 0 ? style.indexColorBlack : style.indexColorWhite;
    ctx.fillText(text, cs * pos[0], cs * pos[1]);
  }

  if (position.length > startIndex) {
    let pos = position[position.length - 1];
    if (highlight) ctx.fillStyle = style.indexColorLast;
    else
      ctx.fillStyle =
        position.length % 2 == 1
          ? style.indexColorBlack
          : style.indexColorWhite;
    ctx.fillText(position.length - startIndex, cs * pos[0], cs * pos[1]);
  }

  ctx.restore();
}

function drawLastStep(ctx, style, cs, position) {
  if (position.length == 0) return;
  ctx.save();
  ctx.fillStyle = style.lastStepColor;
  ctx.translate(paddingX + cs / 2, paddingTop + cs / 2);
  ctx.scale(cs, cs);
  let pos = position[position.length - 1];
  fillCircle(ctx, pos[0], pos[1], style.lastStepScale);
  ctx.restore();
}

function drawWinline(ctx, style, cs, winline) {
  if (winline.length == 0) return;

  ctx.save();
  ctx.lineCap = "round";
  ctx.strokeStyle = style.winlineColor;
  ctx.lineWidth = style.winlineWidth;
  ctx.translate(paddingX + cs / 2, paddingTop + cs / 2);
  ctx.scale(cs, cs);

  ctx.beginPath();
  ctx.moveTo(winline[0][0], winline[0][1]);
  ctx.lineTo(winline[1][0], winline[1][1]);
  ctx.stroke();

  ctx.restore();
}

function drawRealtime(ctx, style, cs, moves) {
  ctx.translate(paddingX + cs / 2, paddingTop + cs / 2);
  ctx.scale(cs, cs);

  ctx.fillStyle = style.thoughtMoveColor;
  for (let p of moves.thought) {
    fillCircle(ctx, p[0], p[1], style.realtimeMoveScale);
  }

  ctx.fillStyle = style.lostMoveColor;
  for (let p of moves.lost) {
    fillCircle(ctx, p[0], p[1], style.realtimeMoveScale);
  }

  ctx.fillStyle = style.thinkingMoveColor;
  for (let p of moves.thinking) {
    fillCircle(ctx, p[0], p[1], style.realtimeMoveScale);
  }

  ctx.fillStyle = style.bestMoveColor;
  for (let p of moves.best) {
    fillCircle(ctx, p[0], p[1], style.bestMoveScale);
  }
}

function drawSelection(ctx, style, cs, pos) {
  ctx.save();
  ctx.strokeStyle = style.selectionStrokeColor;
  ctx.lineWidth = style.selectionStrokeWidth;
  ctx.translate(paddingX + cs / 2, paddingTop + cs / 2);
  ctx.scale(cs, cs);

  ctx.beginPath();
  ctx.moveTo(pos[0] - 0.5, pos[1] - 0.5);
  ctx.lineTo(pos[0] + 0.5, pos[1] - 0.5);
  ctx.lineTo(pos[0] + 0.5, pos[1] + 0.5);
  ctx.lineTo(pos[0] - 0.5, pos[1] + 0.5);
  ctx.closePath();
  ctx.stroke();

  ctx.restore();
}

export default {
  name: "Board",
  props: {},
  data: function() {
    return {
      debouncedRedrawAllLayers: debounce(50, this.redrawAllLayers),
      throttledDrawRealtimeLayer: throttle(10, this.drawRealtimeLayer),
      selecting: false,
      selectedCoord: [0, 0],
      pressed: false,
      cancelMouse: false
    };
  },
  computed: {
    ...mapState("position", {
      boardSize: "size",
      position: "position",
      winline: "winline"
    }),
    ...mapState("settings", [
      "boardStyle",
      "indexOrigin",
      "showCoord",
      "showDetail",
      "showIndex",
      "showLastStep",
      "showWinline",
      "clickCheck"
    ]),
    ...mapState("ai", {
      realtime: state => state.outputs.realtime
    }),
    ...mapGetters("position", ["isInBoard"]),
    context() {
      return idx => {
        return this.$refs["canvas" + idx].getContext("2d");
      };
    },
    canvasWidth() {
      return Math.min(
        this.$store.state.screenWidth,
        this.$store.state.screenHeight - 140
      );
    },
    canvasHeight() {
      return this.canvasWidth - 2 * paddingX + paddingBottom + paddingTop;
    },
    boardWidth() {
      return this.canvasWidth - 2 * paddingX;
    },
    canvasStyle() {
      return {
        width: this.canvasWidth + "px",
        height: this.canvasHeight + "px"
      };
    },
    renderRatio() {
      // (DPI)
      return window.devicePixelRatio || 1;
    },
    renderWidth() {
      return this.canvasWidth * this.renderRatio;
    },
    renderHeight() {
      return this.canvasHeight * this.renderRatio;
    }
  },
  methods: {
    drawBoardLayer(ctx, noclear) {
      ctx = ctx || this.context(0);
      let width = this.canvasWidth;
      let height = this.canvasHeight;
      let cellSize = this.boardWidth / this.boardSize;

      ctx.save();
      ctx.scale(this.renderRatio, this.renderRatio);
      if (!noclear) ctx.clearRect(0, 0, width, height);

      drawBackground(ctx, this.boardStyle, width, height);
      if (this.showCoord)
        drawCoord(
          ctx,
          this.boardStyle,
          width,
          height,
          this.boardSize,
          cellSize
        );
      drawBoard(ctx, this.boardStyle, this.boardSize, cellSize);

      ctx.restore();
    },
    drawPieceLayer(ctx, noclear) {
      ctx = ctx || this.context(1);
      let cellSize = this.boardWidth / this.boardSize;
      ctx.save();
      ctx.scale(this.renderRatio, this.renderRatio);
      if (!noclear) ctx.clearRect(0, 0, this.canvasWidth, this.canvasHeight);

      drawPiece(ctx, this.boardStyle, cellSize, this.position);
      if (this.showWinline)
        drawWinline(ctx, this.boardStyle, cellSize, this.winline);
      if (this.showIndex)
        drawIndex(
          ctx,
          this.boardStyle,
          cellSize,
          this.position,
          this.indexOrigin,
          this.showLastStep
        );
      else if (this.showLastStep)
        drawLastStep(ctx, this.boardStyle, cellSize, this.position);

      ctx.restore();
    },
    drawRealtimeLayer() {
      let ctx = this.context(2);
      let cellSize = this.boardWidth / this.boardSize;
      ctx.save();
      ctx.scale(this.renderRatio, this.renderRatio);
      ctx.clearRect(0, 0, this.canvasWidth, this.canvasHeight);

      if (this.selecting)
        drawSelection(ctx, this.boardStyle, cellSize, this.selectedCoord);
      else if (this.showDetail)
        drawRealtime(ctx, this.boardStyle, cellSize, this.realtime);

      ctx.restore();
    },
    redrawAllLayers() {
      this.drawBoardLayer();
      this.drawPieceLayer();
      this.drawRealtimeLayer();
    },
    screenshot() {
      let ctx = this.context(3);
      ctx.save();
      ctx.scale(this.renderRatio, this.renderRatio);
      ctx.fillStyle = "#FFFFFF";
      ctx.fillRect(0, 0, this.canvasWidth, this.canvasHeight);
      ctx.restore();

      this.drawBoardLayer(ctx, true);
      this.drawPieceLayer(ctx, true);
      return this.$refs.canvas3.toDataURL("image/jpeg");
    },
    onMouseDown(event) {
      if (this.clickCheck == 2) {
        if (event.type === "touchstart") {
          this.cancelMouse = true;
          event.button = 0;
          if (!event.pageX) event.pageX = event.touches[0].pageX;
          if (!event.pageY) event.pageY = event.touches[0].pageY;
        } else if (this.cancelMouse) {
          this.cancelMouse = false;
          return;
        }
      }

      let x = event.pageX - this.$refs.canvas0.getBoundingClientRect().left;
      let y = event.pageY - this.$refs.canvas0.getBoundingClientRect().top;
      let cellSize = this.boardWidth / this.boardSize;
      x = Math.floor((x - paddingX) / cellSize);
      y = Math.floor((y - paddingTop) / cellSize);

      if (event.button != 0) {
        this.$emit("clicked", { x, y, button: event.button });
        return;
      } else if (this.winline.length > 0) return;

      if (this.clickCheck == 0)
        this.$emit("clicked", { x, y, button: event.button });
      else if (this.clickCheck == 1) {
        if (this.selecting) {
          let dx = Math.abs(x - this.selectedCoord[0]);
          let dy = Math.abs(y - this.selectedCoord[1]);
          if (Math.max(dx, dy) <= 1)
            this.$emit("clicked", {
              x: this.selectedCoord[0],
              y: this.selectedCoord[1],
              button: event.button
            });
        } else {
          this.selectedCoord = [x, y];
        }
        this.selecting = !this.selecting;
      } else if (this.clickCheck == 2) {
        this.pressed = true;
        this.selectedCoord = [x, y - 2];
        this.selecting = this.isInBoard(this.selectedCoord);
      }
    },
    onMouseUp(event) {
      if (!this.pressed) return;

      if (event.type === "touchend") {
        event.preventDefault();
        event.button = 0;
      }

      this.$emit("clicked", {
        x: this.selectedCoord[0],
        y: this.selectedCoord[1],
        button: event.button
      });

      this.selecting = false;
      this.pressed = false;
    },
    onMouseMove(event) {
      if (!this.pressed) return;

      if (event.type === "touchmove") {
        event.preventDefault();
        event.button = 0;
        if (!event.pageX) event.pageX = event.touches[0].pageX;
        if (!event.pageY) event.pageY = event.touches[0].pageY;
      }

      let x = event.pageX - this.$refs.canvas0.getBoundingClientRect().left;
      let y = event.pageY - this.$refs.canvas0.getBoundingClientRect().top;
      let cellSize = this.boardWidth / this.boardSize;
      x = Math.floor((x - paddingX) / cellSize);
      y = Math.floor((y - paddingTop) / cellSize);

      let oldCoord = this.selectedCoord;
      this.selectedCoord = [x, y - 2];
      this.selecting = this.isInBoard(this.selectedCoord);

      if (
        this.selecting &&
        (oldCoord[0] != this.selectedCoord[0] ||
          oldCoord[1] != this.selectedCoord[1])
      ) {
        this.throttledDrawRealtimeLayer();
      }
    }
  },
  watch: {
    "$store.state.screenWidth": function() {
      this.debouncedRedrawAllLayers();
    },
    "$store.state.screenHeight": function() {
      this.debouncedRedrawAllLayers();
    },
    boardSize() {
      this.redrawAllLayers();
    },
    position: {
      handler() {
        this.drawPieceLayer();
      },
      deep: true
    },
    realtime: {
      handler() {
        if (this.showDetail) this.throttledDrawRealtimeLayer();
      },
      deep: true
    },
    indexOrigin() {
      this.drawPieceLayer();
    },
    showCoord() {
      this.drawBoardLayer();
    },
    showDetail() {
      this.drawRealtimeLayer();
    },
    showIndex() {
      this.drawPieceLayer();
    },
    showLastStep() {
      this.drawPieceLayer();
    },
    showWinline() {
      this.drawPieceLayer();
    },
    selecting() {
      this.throttledDrawRealtimeLayer();
    }
  },
  mounted() {
    this.redrawAllLayers();
    window.addEventListener("mousemove", throttle(50, this.onMouseMove));
    window.addEventListener("mouseup", this.onMouseUp);
    window.addEventListener("touchmove", throttle(50, this.onMouseMove), { passive: false });
    window.addEventListener("touchend", this.onMouseUp, { passive: false });
  }
};
